export default {
  name: 'show-more-button',
  props: {
    count: {
      type: Number,
      default: 1
    },
    showMore: { type: Function },
  },
  data() {
    return {
      toggle: false,
    }
  },
  computed: {
    buttonText(){
      return this.toggle ? '- Show Less' : '+ Show More';
    }
  },
  methods: {
    toggleButton() {
      this.toggle = !this.toggle
    }
  }
};
