import RelatedRule from './RelatedRule.vue'
import ShowMoreButton from "./ShowMoreButton.vue";
export default {
    name: 'related-rule-list',

    components: {
        RelatedRule,
        ShowMoreButton
    },

    props: {
        rules: Array,
        limit: {
          type: Number,
          default: 5
        },
        title: {
          type: String,
        }

    },

    computed: {
        limitedRules() {
            if (this.limitedList) {
                return this.rules.slice(0, this.limit);
            }
            return this.rules;
        },
        rulesCount() {
            return this.rules.length;
        },
    },

    data() {
      return {
          limitedList: true,
      };
    },

    methods: {
        showMore() {
            this.limitedList = !this.limitedList;
        },
    },

    filters: {

    },
};
